@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  box-sizing: border-box;
  font-family: Quicksand;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

input {
  font-family: Quicksand;
}

body {
  background-color: rgb(250, 250, 250);
}
.App {
  background-color: rgb(250, 250, 250);
}
.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(82, 85, 90);
  background: linear-gradient(rgb(206, 206, 206), rgb(47, 47, 47));
  height: calc(100vh - 70px);
}

.home-container > div {
  margin: 0 auto;
}

.home-image {
  width: 80vw;
  max-width: 600px;
  margin: 0 0 60px 0;
}

.home-container h1 {
  color: rgb(235, 235, 235);
  text-align: center;
  font-size: min(6vw, 50px);
  line-height: 1.5;
  text-shadow: 1px 1px 1px black;
  text-transform: capitalize;
}
.store-container {
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.store-item {
  padding: 10px;
  padding-bottom: 2px;
  margin: 5px;
  width: calc(50% - 10px);
  cursor: pointer;
  text-decoration: none;
  color: black;
}

.store-item:hover {
  background-color: rgb(235, 235, 235);
}

@media (min-width: 600px) {
  .store-container {
    max-width: 1500px;
    margin: 40px auto;
  }

  .store-item {
    width: 300px;
  }
}

.product-image-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 160px;
}

@media (min-width: 600px) {
  .product-image-container {
    height: 250px;
  }
}

.product-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.product-details {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.product-details h2 {
  font-size: 1.3rem;
  padding: 5px;
  padding-left: 10px;
  font-weight: 400;
}

.product-details h2:last-child {
  font-weight: 700;
}

.cart-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 5px;
}

@media (min-width: 600px) {
  .cart-container {
    margin: 50px auto;
  }
}


.cart-item {
  text-decoration: none;
  color: black;
  display: grid;
  grid-template-columns: 30% 30% 30% 10%;
  margin: 10px 0;
}

.cart-item:hover {
  background-color: rgb(235, 235, 235);
}

@media (min-width: 600px) {
  .cart-item {
    grid-template-columns: 1fr 2fr 1fr 1fr;
  }
}

.cart-item div {
  padding: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cart-item .cart-image-container {
  padding: 0;
  max-height: 150px;

}

.cart-image {
  height: 100%;
  width: 100%;
  max-width: 100%;
}

.cart-item .cart-details-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: space-around;
}

@media (min-width: 600px) {

  .cart-item .cart-image-container {
    margin-right: auto;
    min-width: 200px;
  }

  .cart-image {
    max-height: 150px;
    width: auto;
  }

  .cart-item .cart-details-container {
    flex-direction: row;
  }
}

.remove-button {
  height: 50px;
  width: 50px;
  color: black;
  font-size: 1.5rem;
  outline: none;
  border: none;
  background-color: transparent;
  padding: 10px;
  cursor: pointer;
  margin-right: 10px;
}

.remove-button:hover {
  color: red;
}

.total-container {
  margin-top: 50px;
  display: grid;
  grid-template-columns: 30% 30% 30% 10%;
  border-top: 1px solid black;
}

@media (min-width: 600px) {
  .total-container {
    grid-template-columns: 1fr;
  }

  .total-container div {
    margin-left: auto;
  }
}

.total-container div {
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 10px;
}

.total-container h1, .total-container button, .total-container h2 {
  margin: 10px;
  font-size: 1.3rem;
}

.total-container h2 {
  font-size: 1.5rem;
  font-weight: 700;
}

.total-container button {
  font-size: 1.1rem;
  padding: 10px 30px;
  background-color: rgb(36, 35, 35);
  color: white;
  text-transform: uppercase;
  border: none;
  outline: none;
  cursor: pointer;
}

.total-container button:hover {
  background-color: rgb(85, 85, 85);
}


.Nav {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

.Nav ul {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  background-color: rgb(36, 35, 35);
  color: white;
  text-transform: uppercase;
  text-align: center;
}

.Nav li {
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.Nav a {
  text-decoration: none;
  color: white;
  display: flex;
  padding: 20px 20px;
  font-weight: 700;
  width: 100%;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.Nav a:hover {
  background-color: rgb(77, 77, 77);
}

.Nav a:focus {
  background-color: transparent;
}

.logo {
  width: 120%;
  max-width: 120px;
}

@media (min-width: 600px) {
  .logo {
    max-height: 25px;
  }
}

.cart-icon {
  width: 22px;
  height: 22px;
  margin-top: 2px;
}

.cart-quantity {
  min-width: 20px;
  height: 20px;
  font-size: 16px;
  line-height: 12px;
  margin-bottom: 10px;
  font-weight: 700;
  color: rgb(255, 255, 255);
  background-color: rgb(255, 0, 0);
  padding: 4px;
  border-radius: 100px;
  -webkit-border-radius: 100px;
  -moz-border-radius: 100px;
  -ms-border-radius: 100px;
  -o-border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-container {
  padding: 10px;
  max-width: 600px;
}

@media (min-width: 600px) {
  .product-container {
    max-width: 650px;
    margin: 0 auto;
  }
}

.product-name {
  font-weight: 700;
  text-align: center;
  font-size: 1.7rem;
  padding: 8px 0;
}

.product-price {
  font-size: 1.5rem;
  padding: 10px;
  text-align: center;
}

.showcase-image-container {
  max-width: 600px;
  margin: 0 auto;
}

.showcase-image {
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.product-form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.product-form input {
  padding: 10px;
  font-size: 1.2rem;
  margin: 10px 0;
}

.product-form label {
  padding: 0 5px;
  font-size: 1.2rem;
}

.product-form button {
  font-size: 1.1rem;
  padding: 15px 30px;
  margin: 10px 0;
  background-color: rgb(36, 35, 35);
  color: white;
  text-transform: uppercase;
  border: none;
  outline: none;
  cursor: pointer;
  width: 100%;
}

@media (min-width: 600px) {
  .product-form button {
    width: 350px;
  }
}

.product-form button:hover {
  background-color: rgb(85, 85, 85);
}

.product-number {
  font-size: 1.2rem;
  padding: 10px 0;
}

